@import url(https://fonts.googleapis.com/css?family=Roboto|Staatliches);
* {
  box-sizing: border-box; }

body {
  margin: 0; }

a {
  text-decoration: none; }

.container {
  font-family: 'Roboto', sans-serif;
  display: grid; }

.navbar {
  background-color: #008ECC; }

.navbar-menu {
  text-align: center; }

.navbar-logo {
  font-size: 26px;
  margin: 7px;
  letter-spacing: 2px;
  font-family: 'Staatliches', cursive;
  color: white; }

footer {
  background-color: #1e1f26;
  text-align: center; }

.copyright {
  padding: 8px; }
  .copyright p {
    font-size: 14px;
    letter-spacing: 2px;
    color: #dbdbdb;
    margin: 0px; }

.chart-line-main {
  stroke: #272727;
  stroke-width: 1.5; }

.chart-line-grid {
  stroke: #666666;
  stroke-width: 0.2; }

.chart-label {
  font-size: 10.5px; }

.content {
  width: 100%;
  display: flex;
  display: -webkit-flex;
  flex-direction: row; }
  @media (max-width: 576px) {
    .content {
      flex-direction: column;
      justify-content: space-between; } }

.content-main {
  width: 100%;
  height: 88vh;
  display: flex;
  display: -webkit-flex;
  flex-direction: column;
  justify-content: space-between; }
  @media (max-width: 576px) {
    .content-main {
      height: 35vh;
      justify-content: center; } }
  @media (max-width: 576px) and (max-height: 768px) {
    .content-main {
      height: 63vh; } }
  @media (max-width: 576px) and (max-height: 360px) {
    .content-main {
      height: 75vh; } }

.content-main-header {
  padding: 5px 30px; }
  .content-main-header h1 {
    margin: 10px 0px; }
  @media (max-width: 576px) {
    .content-main-header {
      display: none; } }
  @media (max-height: 375px) {
    .content-main-header > h1 {
      display: none; } }

.content-main-content {
  padding: 10px 30px; }
  @media (max-width: 576px) {
    .content-main-content {
      padding: 10px; } }

.content-loading {
  font-size: 25px;
  letter-spacing: 5px;
  height: 100vh;
  width: 100vw;
  z-index: 1000;
  position: absolute;
  top: 0;
  left: 0;
  background-color: white;
  color: #242424;
  display: flex;
  display: -webkit-flex;
  justify-content: center;
  align-items: center;
  -webkit-box-align: center;
  -moz-box-align: center;
  -ms-flex-align: center;
  -webkit-align-items: center; }

.sidebar {
  width: 240px;
  height: 590px;
  background-color: #e0e0e0;
  color: #252525;
  overflow-x: hidden;
  overflow-y: scroll; }
  @media (max-width: 576px) {
    .sidebar {
      width: 100%;
      height: 65vh; } }
  @media (max-width: 576px) and (max-height: 768px) {
    .sidebar {
      height: 50vh; } }
  @media (max-width: 576px) and (max-height: 360px) {
    .sidebar {
      height: 25vh;
      display: flex;
      display: -webkit-flex;
      overflow-x: scroll; } }

.sidebar-search {
  padding: 10px;
  position: sticky;
  position: -webkit-sticky;
  position: -moz-sticky;
  position: -ms-sticky;
  position: -o-sticky;
  background: lightgray;
  top: 0; }

.sidebar-search-input {
  margin: 10px 0;
  width: 155px;
  height: 30px;
  background: #fff;
  border: 2px solid #ccc;
  border-radius: 4px;
  font-size: 14px; }
  .sidebar-search-input :focus {
    outline: none;
    border: 2px solid green; }
  @media (max-width: 576px) {
    .sidebar-search-input {
      width: 80%; } }
  @media (max-width: 576px) and (max-height: 360px) {
    .sidebar-search-input {
      width: auto;
      height: 20px; } }

.sidebar-card {
  height: 40px;
  padding: 10px;
  cursor: pointer; }
  @media (max-width: 576px) {
    .sidebar-card {
      margin: 5px 0px; } }
  .sidebar-card p {
    font-weight: bold; }
    @media (max-width: 576px) {
      .sidebar-card p {
        text-align: center; } }
  .sidebar-card :hover {
    color: #9aa1fa; }

